import request from 'utils/request'
/**
 * 管理员模块
 */
// 终端机管理-已分发
export function getAdminMachine (data) {
  return request({
    method: 'post',
    url: '/goodfield/web/admin/getAdminMachine',
    data
  })
}
// 终端机管理-未分发
export function getAdminMachineDistribute (data) {
  return request({
    method: 'post',
    url: '/goodfield/web/admin/getAdminMachineDistribute',
    data
  })
}

export function getMemory () {
  return request({
    method: 'post',
    url: '/goodfield/web/admin/getMemorys',
    data: {}
  })
}
/** **************************************华丽的分割线**************************************************/

/**
 * 用户模块
 */
// 终端机管理
export function getMachineInfo (data) {
  return request({
    method: 'post',
    url: '/goodfield/web/user/getMachineInfo',
    data
  })
}

// 获取指定用户三屏显示的终端机列表
export function getMachineListByUserIdForScreen (data) {
  return request({
    method: 'post',
    url: '/goodfield/web/folder/getstoresNameMachine',
    data
  })
}



